@import "./import.scss";
@import "./components/form.scss";
@import "./components/product.scss";
.detail{
    margin-top: 40px;
    &__img{
        width: 50%;
        &-main{
            border: 1px solid #ebebeb;
            img{
                width: 100%;
            }
            .elem {
                display: inline-block;
                font-size: 0;
                width: 100%;
                border-bottom: none;
                background: #fff;
                background-clip: padding-box;
            }
            .elem>img {
                display: block;
                cursor: pointer;
                width: 100%;
            }
            .elem-none{
                display: none;
            }
        }
        &-thumb{
            margin-top: 20px;
            padding: 0 20px;
            position: relative;
            i{
                display: none;
                position: absolute;
                top: 32%;
                height: 30px;
                width: 30px;
                z-index: 20;
                color: white;
                line-height: 30px;
                text-align: center;
                background-color: #f72b3f;
                &:hover {
                    background-color: #060085;
                }
            }
            .prev-big-img{
                left: -10px;
            }
            .next-big-img{
                right: -10px;
            }
            img{
                width: 96px;
                max-width: 100%;
            }
        }
    }
    &__infor{
        width: 50%;
        &-head{
            font-weight: 700;
            font-size: 24px;
            margin-bottom: 10px;
        }
        &-branch{
            display: flex;
            margin-bottom: 20px;
            .branch{
                span:nth-child(2){
                    color: #008000;
                    margin-left: 5px;
                }
                &:nth-child(1)::after{
                    content: "/";
                    margin-left: 7px;
                    margin-right: 10px;
                }
            }
        }
        &-describle{
            color : #898989;
            padding-bottom: 40px;
            border-bottom: 1px solid #ebebeb;
            margin-bottom: 20px;
        }
        &-price{
            color: #f72b3f;
            font-size: 28px;
            margin-bottom: 30px;
        }
        &-size{
            margin-bottom: 20px;
            .size-head{
                color: #707070;
                margin-bottom: 10px;
            }
            .size-list{
                input[type="checkbox"],
                input[type="radio"] {
                    display: inline;
                    margin: 0;
                    padding: 0;
                    width: 100%;
                    height: 36px;
                    position: absolute;
                    background: transparent;
                    outline: none;
                    opacity: 0;
                }
                label {
                    display: inline-block;
                    height:36px;
                    margin-right: 5px;
                    padding: 0 21px;
                    line-height: 34px;
                    text-align: center;
                    border: 2px solid #ebebeb;
                }
                input:checked +label {
                    border-color: #ff2a2d;
                    color: #ff2a2d;
                    font-weight: 400;
                }
                [class*="hint--"]:after {
                    background-color: #ff2a2d;
                    width: 100px;
                    height: 40px;
                    line-height: 28px;
                    text-align: center;
                    color: white;
                  }
                  .hint--top:before {
                    border-top-color: #ff2a2d;
                  }
            }
            #white{
                background-color: #fff;
            }
            #black{
                background-color: black;
            }
        }
        &-color{
            .size-list{
                label{
                    width: 60px;
                }
            }
        }
        &-number{
            display: flex;
            .number-count {
                display: flex;
        
                input::-webkit-outer-spin-button,
                input::-webkit-inner-spin-button {
                  -webkit-appearance: none;
                  margin: 0;
                }
        
                input {
                  height: 55px;
                  text-align: center;
                  width: 85px;
                  color: rgb(83, 83, 83);
                  border: 1px solid #ebebeb;
                }
        
                span {
                  height: 55px;
                  line-height: 55px;
                  width: 45px;
                  color: #707070;
                  text-align: center;
                  cursor: pointer;
                  font-size: 14px;
                }
        
                .num-decrease {
                  border-right: none;
                }
        
                .num-increase {
                  border-left: none;
                }
              }
              .number-btn{
                  margin-left: 10px;
                  a{
                  height: 55px;
                  line-height: 55px;
                  background-color: #f72b3f;
                  color: white;
                  font-weight: 700;
                  font-size: 20px;
                  width: 230px;
                  text-align: center;
                  display: block;
                  &:hover{
                      background-color: #060085;
                      color: white;
                  }
                    }
              }
        }
        &-hotline{
            margin-top: 30px;
            border-top: 1px solid #ebebeb;
            padding-top: 20px;
            display: flex;
            align-items: center;
            justify-content:space-between;
            .hotline-number{
                a{
                    color: #f72b3f;
                    font-size: 20px;
                    margin-left: 5px;
                    &:hover{
                        color:#060085;
                    }
                }
            }
            .hotline-pay{
                img{
                    max-width: 100%;
                }
            }
        }
    }
}

.banner{
    border-top: 1px solid #ebebeb;
    margin-top: 30px;
    padding:40px 0;
    img{
        width: 100%;
    }
}
.tab{
    &__head{
        display: flex;
        justify-content: center;
        margin-bottom: 50px;
        div{
            padding: 10px 30px;
            cursor: pointer;
            margin: 0px 10px 0 0;
            border: 2px solid #ebebeb;
            font-size: 18px;
        }
        &-select{
            border-color: #f72b3f !important;
        }
    }
    &__content-1{
        h2{
            font-size: 24px;
            color: #707070;
        }
        p{
            color: #707070;
            margin-bottom: 15px;
        }
    }
    &__content-2{
        display: none;
        p{
            color: #707070;
            margin-bottom: 15px;
        }
    }
    &__content-3{
        display: none;
.question {
    &-user {
      abbr {
        padding: 9px 7px;
        color: white;
        font-weight: 700;
        text-align: center;
        background-color: #595959;
        border-radius: 50%;
        text-decoration: none;
      }
      span {
        font-weight: 700;
      }
    }
    &-ques {
      margin-top: 15px;
      margin-bottom: 15px;
      &__star {
        color: #ffcc00;
        i {
          margin: 0 -1px;
          font-size: 13px;
        }
      }
    }
  }
  .question-answer {
    display: flex;
    align-items: center;
    .answer,
    .time {
      font-size: 13px;
    }
    .answer:hover {
      color: #e51515;
      cursor: pointer;
    }
    .dot {
      width: 4px;
      height: 4px;
      background-color: #333;
      border-radius: 50%;
      margin: 0 7px;
    }
  }
  .question-admin-1 {
    display: none;
  }
  .answers {
    margin-top: 15px;
    margin-bottom: 10px;
    background: #f5f5f5;
    border: 1px solid #e5e5e5;
    border-radius: 2px;
    padding: 10px 12px 5px;
    position: relative;
    &::after {
      position: absolute;
      content: "";
      width: 10px;
      height: 10px;
      background: #f5f5f5;
      border: 1px solid #e5e5e5;
      top: -7px;
      left: 15px;
      border-bottom: none;
      border-right: none;
      transform: rotateZ(45deg);
    }
    &-admin {
      display: flex;
      align-items: center;
      img {
        border-radius: 50%;
        width: 30px;
        height: 30px;
      }
      .name {
        font-weight: 700;
        font-size: 14px;
        margin: 0 10px;
      }
      .position {
        background: linear-gradient(#f59000, #fd6e1d);
        border-radius: 2px;
        color: #fff;
        display: inline-block;
        font-size: 13px;
        font-weight: normal;
        margin: 5px 0 0 10px;
        padding: 3px 5px 3px;
        vertical-align: top;
      }
    }
    &-comment {
      line-height: 24px;
      margin-top: 5px;
      margin-bottom: 7px;
      .link {
        color: #e51515;
      }
      .phone {
        background: linear-gradient(#52b858, #34a853);
        color: white;
        padding: 2px 4px;
        border-radius: 4px;
      }
    }
  }
  .customer-ques {
    margin-top: 50px;
  }
  .comment {
    .news__comment-head {
      font-size: 18px;
      font-weight: 700;
      padding-bottom: 10px;
      border-bottom: 1px solid #eaebf3;
      margin-bottom: 20px;
    }
  }
    }
}
.like{
    margin-top: 100px;
    margin-bottom: 60px;
    .container-ct{
        background-image: url(https://bizweb.dktcdn.net/100/369/492/themes/741260/assets/bg_hotline.jpg?1572516554954);
        background-size: cover;
        background-position:  center;
        padding: 100px 0 90px;
    }
    &__title {
        margin-top: 20px;
        margin-bottom: 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        a {
          text-transform: uppercase;
          font-size: 26px;
          color: white;
          padding: 0 10px;
          margin: 0 10px;
          white-space: nowrap;
          background-color: transparent;
          &:hover {
            color: #f72b3f;
          }
        }
          span {
              display: block;
            width: 100%;
            height: 1px;
            background-color: #e8e8e8;
          }
        }
    &__list {
        padding: 25px 50px 50px 50px;
        border: 1px solid #fff;
        margin: 0 150px;
        position: relative;
        .prev-big,
        .next-big {
          display: none;
          position: absolute;
          top: 50%;
          height: 40px;
          width: 40px;
          z-index: 20;
          color: white;
          line-height: 40px;
          text-align: center;
          background-color: #f72b3f;
          &:hover {
            background-color: #060085;
          }
        }
        .prev-big {
          left: 30px;
          cursor: pointer;
        }
        .next-big {
          right: 30px;
          cursor: pointer;
        }
        &:hover {
          .prev-big,
          .next-big {
            display: block;
          }
        }
      }
      .product{
          background-color: white ;
          padding-bottom: 20px;
      }
}

@media screen and (max-width:1200px){
    .like{
        &__list{
            border: 1px solid white;
            margin: 0 15px;
            padding: 0;
            padding-bottom: 60px;
            .product{
                &__slide{
                    display: none;
                }
                &__infor{
                    margin-top: 20px;
                }
            }
        }
        &__title{
            margin-top: 60px;
            margin-bottom: 40px;
        }
    }
}
@media screen and (max-width:992px){
    .detail{
        .row-ct{
            flex-wrap: wrap;
        }
        &__img{
            width: 100%;
        }
        &__infor{
            width: 100%;
        }
    }
    .zoomContainer{
        display: none;
    }
}

@media screen and (max-width:768px){
    .detail{
        &__infor{
            &-branch{
                flex-wrap: wrap;
                .branch{
                    width: 100%;
                    &:nth-child(1)::after{
                        content: "";
                    }
                }
            }
            &-number{
                flex-wrap: wrap;
            .number-count{
                width: 100%;
            }
            .number-btn{
                width: 100%;
                margin-left: 0;
                margin-top: 20px;
                a{
                    width: 100%;
                }
            }
            }
            &-hotline{
                flex-wrap: wrap;
                .hotline-pay{
                    width: 100%;
                    margin-top: 10px;
                }
                .hotline-number{
                    width: 100%;
                }
            }
        }
        &__img{
            &-thumb:hover{
                i{
                    display: block;
                }
            }
        }
    }
    .tab{
        &__head{
            margin-bottom: 20px;
            flex-wrap: wrap;
            div{
                width: 100%;
                text-align: center;
                margin: 10px 0;
            }
        }
    }
    .like{
        &__list{
            .product{
                &__infor{
                    padding: 0 15px;
                }
            }
        }
    }
}